import React, { useState, useEffect } from "react";
import { Link, useNavigate } from "react-router-dom";
import orderService from "./../services/orderService";

const OrderManagement = () => {
  const [orders, setOrders] = useState([]);
  useEffect(() => {
    loadData();
  }, []);
  const loadData = () => {
    orderService.list().then((res) => setOrders(res.data));
  };
  const navigate = useNavigate();
  const showOrderDetail = (e, id) => {
    if (e) e.preventDefault();
    navigate(`/order/${id}`);
    window.scrollTo(0, 0);
  };

  return (
    <>
      <div className="container mt-4">
        <div className="card border-success bt-5">
          <div className="card-header">
            <div className="row">
              <div className="col">
                <h3 className="card-title text-success">Danh Sch n Hng</h3>
              </div>
            </div>
          </div>
          <div className="card-body">
            <div className="table-responsive">
              <table className="table table-bordered border-success table-hover table-striped">
                <thead>
                  <tr className="table-success border-success">
                    <th>STT</th>
                    <th>Tn Khch Hng </th>
                    <th>Ngy Mua Hng </th>
                    <th></th>
                  </tr>
                </thead>
                <tbody>
                  {orders.map((order, idx) => (
                    <tr key={order.Ban_ID}>
                      <td>{idx + 1}</td>
                      <td>
                        {order.customer.lastName} {order.customer.firstName}
                      </td>
                      <td>{order.date}</td>

                      <td>
                        <Link
                          to="/#"
                          className="nav-link text-success "
                          onClick={(e) => showOrderDetail(e, order.Or_ID)}
                        >
                          <i class="fa-solid fa-pen-to-square"></i>
                          Xem Chi Tit
                        </Link>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default OrderManagement;
