import api from "./api";
const list = () => api.get(api.url.productsDetail);
const get = (id) => api.get(`${api.url.productsDetail}/${id}`);
const getType = (id) => api.get(`${api.url.productTypes}/${id}`);
const add = (data) => {
  const formData = new FormData();
  for (const key in data) formData.append(key, data[key]);
  return api.post(api.url.productsDetail, formData, {
    headers: {
      "Content-Type": "multipart/form-data",
    },
  });
};
const update = (id, data) => {
  const formData = new FormData();
  for (const key in data) formData.append(key, data[key]);
  return api.post(`${api.url.productsDetail}/${id}`, formData, {
    headers: {
      "Content-Type": "multipart/form-data",
    },
  });
};
const remove = (id) => api.delete(`${api.url.productsDetail}/${id}`);
const getImage = (id) => {
  return api.get(`${api.url.productsDetail}/get-img/${id}`, {
    responseType: "blob",
  });
};
const getImage1 = (id) => {
  return api.get(`${api.url.productsDetail}/get-img1/${id}`, {
    responseType: "blob",
  });
};
const getImage2 = (id) => {
  return api.get(`${api.url.productsDetail}/get-img2/${id}`, {
    responseType: "blob",
  });
};

const productDetailService = {
  list,
  get,
  getType,
  add,
  update,
  remove,
  getImage,
  getImage1,
  getImage2,
};
export default productDetailService;
