import React, { useState } from "react";
import { Link } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { logout } from "../store/reducers/auth";
import Offcanvas from "react-bootstrap/Offcanvas";

const Header = () => {
  const userInfo = useSelector((state) => state.auth.userInfo);
  const dispatch = useDispatch();
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
  return (
    <>
      {/* Header */}
      <nav className="navbar navbar-expand-lg navbar-dark bg-success bg-gradient">
        <div className="container">
          <Link className="navbar-brand" to="/home">
            <h3>TRANG CH</h3>
          </Link>
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarNav"
            aria-controls="navbarNav"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>

          <div className="collapse navbar-collapse" id="navbarNav">
            <ul className="navbar-nav me-auto">
              <li className="nav-item">
                <Link className="nav-link" to="" onClick={handleShow}>
                  <i className="fa-solid fa-circle-chevron-right me-2 fa-2x pb-2"></i>
                  <span className="text-light h3 pb-3">MENU</span>
                </Link>
              </li>
            </ul>
            <ul className="navbar-nav">
              <li className="nav-item">
                <a className="nav-link " href="/#">
                  <i className="fa-solid fa-user me-2 border rounded-circle p-3 fa-1x "></i>
                  <span className="h5"> welcome to ...{userInfo.fullName}</span>
                </a>
              </li>
            </ul>
          </div>
        </div>
      </nav>

      <Offcanvas show={show} onHide={handleClose}>
        <Offcanvas.Header closeButton>
          <Offcanvas.Title>
            {" "}
            <i className="fa-solid fa-user me-2 border rounded-circle p-3 fa-1x text-success "></i>
            <span className="h4"> {userInfo.fullName}</span>
          </Offcanvas.Title>
        </Offcanvas.Header>
        <Offcanvas.Body>
          <ul className="nav flex-column">
            <li className="nav-item mb-2">
              <Link to="/productmanagement" className="nav-link text-success ">
                {" "}
                <i className="fa fa-tachometer-alt me-2 " />
                Qun l sn phm
              </Link>
            </li>
            <li className="nav-item mb-2">
              <Link to="/ordermanagement" className="nav-link text-success">
                <i className="fa fa-laptop me-2" />
                Qun l n hng
              </Link>
            </li>
            <li className="nav-item mb-2">
              <Link to="/banner" className=" nav-link  text-success ">
                <i className="fa fa-th me-2" />
                Qun l Banner
              </Link>
            </li>

            <li className="nav-item mb-2">
              <Link
                to="/login"
                className=" nav-link text-success "
                onClick={() => dispatch(logout())}
              >
                <i className="fa-solid fa-right-from-bracket " />
                ng Xut
              </Link>
            </li>
          </ul>
        </Offcanvas.Body>
      </Offcanvas>

      {/* new */}
    </>
  );
};

export default Header;
