import React from "react";
const Input = (props) => {
  const {
    id,
    inputRef,
    label,
    labelSize,
    required,
    frmField,
    errMessage,
    onchange,
    ...others
  } = props;
  const labelClass = `col-sm-${labelSize ? labelSize : 2} col-form-label ${
    required ? "required" : ""
  }`;
  const inputClass = `form-control ${errMessage ? "is-invalid" : ""}`;
  return (
    <>
      {" "}
      <div className="row mb-3">
        <label htmlFor={id} className={labelClass}>
          {label}
        </label>
        <div className="col-sm">
          {others["rows"] > 1 ? (
            <textarea
              ref={inputRef}
              id={id}
              {...others}
              {...frmField}
              className={inputClass}
              // onchange={onchange}
            ></textarea>
          ) : (
            <input
              ref={inputRef}
              type="text"
              id={id}
              {...others}
              {...frmField}
              className={inputClass}
            />
          )}
          {/* check wrong message.  */}
          {errMessage ? (
            <div className="invalid-feedback">{errMessage}</div>
          ) : (
            ""
          )}
        </div>
      </div>
    </>
  );
};

export default Input;
