import { Link } from "react-router-dom";
import React, { useState, useEffect, useRef } from "react";
import productDetailService from "./../services/productDetailService";
import { Modal } from "react-bootstrap";
import { Button } from "react-bootstrap";
import { toast } from "react-toastify";
import Input from "./../components/Input";
import * as Yup from "yup";
import { useFormik } from "formik";
import { CKEditor } from "@ckeditor/ckeditor5-react";
import FullEditor from "ckeditor5-build-full";
import productService from "./../services/productService";
import api from "./../services/api";

const ProductManagement = () => {
  const defaultAvatar =
    "http://localhost/shopmypham/public/data/default-avatar.jpg";
  const [products, setProducts] = useState([]);
  const [productsType, setProductsType] = useState([]);
  const [avatarPreview, setAvatarPreview] = useState(defaultAvatar);
  const [img1Preview, setImg1Preview] = useState(defaultAvatar);
  const [img2Preview, setImg2Preview] = useState(defaultAvatar);
  const avatarFileRef = useRef();
  const img1FileRef = useRef();
  const img2FileRef = useRef();
  useEffect(() => {
    loadData();
  }, []);

  const loadData = () => {
    const productReq = productDetailService.list();
    const typeReq = productService.list();
    api.promise([productReq, typeReq]).then(
      api.spread((...res) => {
        setProducts(res[0].data);
        setProductsType(res[1].data);
      })
    );
  };

  const [modalShow, setModalShow] = useState(false);
  const handleModalClose = () => setModalShow(false);
  const handleModalShow = () => setModalShow(true);

  const formik = useFormik({
    initialValues: {
      ProDe_ID: 0,
      proName: "",
      proDescription: "",
      proIngredient: "",
      proVolume: "",
      proBrand: "",
      proUnit: 0,
      Pro_ID: 0,
      product: {},
      avatar: undefined,
      img1: undefined,
      img2: undefined,
    },
    validationSchema: Yup.object({
      proName: Yup.string().required("Required"),
      proBrand: Yup.string().required("Required"),
      // proDescription: Yup.string().required("Required"),
      //  proIngredient: Yup.string().required("Required"),
      proUnit: Yup.string().required("Required"),
      proPrice: Yup.number().required("Required"),
      proVolume: Yup.number().required("Required"),
    }),
    onSubmit: (values) => {
      console.log(values);
      handleSubmit(values);
    },
  });

  const showEditModal = (e, id) => {
    if (e) e.preventDefault();
    if (id > 0) {
      productDetailService.get(id).then((res) => {
        if (res.errorCode === 0) {
          formik.setValues(res.data);
          if (res.data.proAvatar.length > 0)
            setAvatarPreview(res.data.proAvatar);
          else setAvatarPreview(defaultAvatar);
          if (res.data.proImg1.length > 0) setImg1Preview(res.data.proImg1);
          else setImg1Preview(defaultAvatar);
          if (res.data.proImg2.length > 0) setImg2Preview(res.data.proImg2);
          else setImg2Preview(defaultAvatar);
          handleModalShow();
        }
      });
    } else {
      formik.resetForm();
      setAvatarPreview(defaultAvatar);
      setImg1Preview(defaultAvatar);
      setImg2Preview(defaultAvatar);
      handleModalShow();
    }
  };
  const handleDelete = (e, id) => {
    e.preventDefault();
    productDetailService.remove(id).then((res) => {
      console.log("code " + res.errorCode);
      console.log("code " + res.message);
      if (res.errorCode === 0) {
        toast.warn("Xo sn phm Thnh Cng");
        loadData();
      } else {
        toast.error("Sn Phm Khng Th Xa");
      }
    });
  };
  const handleSubmit = (data) => {
    console.log("aaaaa");
    if (data.ProDe_ID === 0) {
      productDetailService.add(data).then((res) => {
        console.log("code " + res.errorCode);
        console.log("code " + res.message);
        if (res.errorCode === 0) {
          loadData();
          handleModalClose();
        }
      });
    } else {
      productDetailService.update(data.ProDe_ID, data).then((res) => {
        if (res.errorCode === 0) {
          loadData();
          handleModalClose();
          toast.success("Cp Nht Thnh Cng");
        } else {
          toast.error(res.message);
        }
      });
    }
  };

  const handleChangeAvatar = (e) => {
    if (e.target.files && e.target.files[0]) {
      setAvatarPreview(URL.createObjectURL(e.target.files[0]));
      formik.setFieldValue("avatar", e.target.files[0]);
    }
  };
  const handleChangeImg1 = (e) => {
    if (e.target.files && e.target.files[0]) {
      setImg1Preview(URL.createObjectURL(e.target.files[0]));
      formik.setFieldValue("img1", e.target.files[0]);
    }
  };
  const handleChangeImg2 = (e) => {
    if (e.target.files && e.target.files[0]) {
      setImg2Preview(URL.createObjectURL(e.target.files[0]));
      formik.setFieldValue("img2", e.target.files[0]);
    }
  };
  return (
    <>
      <div className="container  ">
        <h1 className="text-center text-success text-opacity-75 mb-3">
          Danh sch sn phm
        </h1>
        <div className="row row-cols-5 gx-1 gy-2">
          {products.map((product) => (
            <div className="col" key={product.ProDe_ID}>
              <div className="card h-100" style={{ width: "12em" }}>
                <div className="text-center pt-3">
                  <img
                    src={product.proAvatar}
                    alt={product.proName}
                    style={{ width: "100px" }}
                  />
                </div>
                <div className="card-body">
                  <div className="row">
                    <div className="col-12">
                      <p className="card-title fs-6 fw-bold">
                        {product.proName}
                      </p>
                    </div>
                    <div className="col-12">
                      {" "}
                      <p className="card-text mb-2">
                        Gi tin:{" "}
                        <span className="text-danger">{product.proPrice}</span>{" "}
                      </p>
                    </div>
                    <div className="row row-cols-2 text-center">
                      <div className="col fs-4">
                        <Link
                          to="/#"
                          onClick={(e) => handleDelete(e, product.ProDe_ID)}
                        >
                          <i className="fa-solid fa-trash text-success"></i>
                        </Link>
                      </div>
                      <div className="col fs-4">
                        <Link
                          to="/#"
                          onClick={(e) => showEditModal(e, product.ProDe_ID)}
                        >
                          <i className="fa-solid fa-pen-to-square text-success"></i>
                        </Link>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          ))}

          <div className="scroll-top fs-1 text-end me-0">
            <Link to="/#" onClick={(e) => showEditModal(e, 0)}>
              <i className="fa-solid fa-file-circle-plus text-success"></i>
            </Link>
          </div>
        </div>
      </div>

      <Modal
        show={modalShow}
        onHide={handleModalClose}
        backdrop="static"
        keyboard={false}
        size="xl"
      >
        <Modal.Header closeButton>
          <Modal.Title>
            {formik.values.ProDe_ID > 0
              ? "Chnh Sa Sn Phm"
              : "Thm Sn Phm"}
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <form>
            <Input
              label="Tn Sn Phm"
              id="name"
              required
              frmField={formik.getFieldProps("proName")}
              errMessage={formik.touched.proName && formik.errors.proName}
            />

            <Input
              label="Gi Tin"
              id="price"
              required
              frmField={formik.getFieldProps("proPrice")}
              errMessage={formik.touched.proPrice && formik.errors.proPrice}
            />
            <Input
              label="Thng Hiu"
              id="brand"
              required
              frmField={formik.getFieldProps("proBrand")}
              errMessage={formik.touched.proBrand && formik.errors.proBrand}
            />
            <Input
              label="Th Tch"
              id="volume"
              required
              frmField={formik.getFieldProps("proVolume")}
              errMessage={formik.touched.proVolume && formik.errors.proVolume}
            />
            <Input
              label="n V"
              id="unit"
              required
              frmField={formik.getFieldProps("proUnit")}
              errMessage={formik.touched.proUnit && formik.errors.proUnit}
            />
            <div className="row mb-3">
              <label className="col-2">Loi</label>
              <div className="col">
                <select
                  className="form-select"
                  {...formik.getFieldProps("Pro_ID")}
                >
                  {productsType.map((type) => (
                    <option value={type.Pro_ID} key={type.Pro_ID}>
                      {type.proType}
                    </option>
                  ))}
                </select>
              </div>
            </div>
            <label className="mb-3">Hnh nh</label>
            <div className="row row-cols-3 mb-3 ">
              <div className="col text-center">
                <img
                  src={avatarPreview}
                  alt=""
                  className="img-thumnail img-fluid"
                  style={{ height: "300px" }}
                />
                <input
                  type={"file"}
                  accept="image/*"
                  className="d-none form-control"
                  ref={avatarFileRef}
                  onChange={handleChangeAvatar}
                ></input>
                <div className="mt-3 ms-0">
                  <Button
                    variant="success"
                    size="sm"
                    onClick={() => avatarFileRef.current.click()}
                  >
                    Thm Hnh
                  </Button>
                </div>
              </div>
              <div className="col text-center">
                <img
                  src={img1Preview}
                  alt=""
                  className="img-thumnail img-fluid"
                  style={{ height: "300px" }}
                />
                <input
                  type={"file"}
                  accept="image/*"
                  className="d-none form-control"
                  ref={img1FileRef}
                  onChange={handleChangeImg1}
                ></input>
                <div className="mt-3 ms-0 ">
                  <Button
                    variant="success"
                    size="sm"
                    onClick={() => img1FileRef.current.click()}
                  >
                    Thm Hnh
                  </Button>
                </div>
              </div>
              <div className="col text-center">
                <img
                  src={img2Preview}
                  alt=""
                  className="img-thumnail img-fluid"
                  style={{ height: "300px" }}
                />
                <input
                  type={"file"}
                  accept="image/*"
                  className="d-none form-control"
                  ref={img2FileRef}
                  onChange={handleChangeImg2}
                ></input>
                <div className="mt-3 ms-0">
                  <Button
                    variant="success"
                    size="sm"
                    onClick={() => img2FileRef.current.click()}
                  >
                    Thm Hnh
                  </Button>
                </div>
              </div>
            </div>
            <label className="mb-3">Miu T</label>
            <CKEditor
              editor={FullEditor}
              data={formik.values.proDescription}
              onBlur={(event, editor) => {
                formik.setFieldValue("proDescription", editor.getData());
              }}
            />

            <label className="mb-3 mt-3">Thnh Phn</label>
            <CKEditor
              editor={FullEditor}
              data={formik.values.proIngredient}
              onBlur={(event, editor) => {
                formik.setFieldValue("proIngredient", editor.getData());
              }}
            />
          </form>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleModalClose}>
            Close
          </Button>
          <Button
            variant="primary"
            onClick={formik.handleSubmit}
            // disabled={!formik.dirty || !formik.isValid}
          >
            Save
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
};

export default ProductManagement;
