import "./App.css";
import { Route, Routes } from "react-router-dom";
import Login from "./pages/Login";
import DefaultLayout from "./containers/DefaultLayout";

function App() {
  return (
    <>
      <Routes>
        <Route path="/login" element={<Login />} />
        <Route path="/*" element={<DefaultLayout />} />
      </Routes>
    </>
  );
}

export default App;
