import React, { useState, useEffect, useRef } from "react";
import { Modal, Button, Row, Col, Pagination } from "react-bootstrap";
import { toast } from "react-toastify";
import bannerService from "./../services/bannerService";
import * as Yup from "yup";
import { useFormik } from "formik";
import Input from "./../components/Input";
import { CKEditor } from "@ckeditor/ckeditor5-react";
import FullEditor from "ckeditor5-build-full";

const Banner = () => {
  const defaultAvatar =
    "http://localhost/shopmypham/public/data/default-avatar.jpg";
  const [avatarPreview, setAvatarPreview] = useState(defaultAvatar);
  const [modalShow, setModalShow] = useState(false);
  const handleModalClose = () => setModalShow(false);
  const handleModalShow = () => setModalShow(true);
  const [banners, setBanners] = useState([]);
  const inputFileRef = useRef();
  const [page, setPage] = useState(0);
  const [pageLength, setPageLength] = useState(2);
  const [pagingItems, setPagingItems] = useState([]);
  const formik = useFormik({
    initialValues: {
      Ban_ID: 0,
      banName: "",
      banDes: "",
      banTime: "",
      banPlace: "",
      banner: undefined,
    },
    validationSchema: Yup.object({
      banName: Yup.string().required("Required").min(5, ">= 5 characters"),
    }),
    onSubmit: (values) => {
      console.log(values);
      handleSubmit(values);
    },
  });

  useEffect(() => {
    loadData();
  }, [page, pageLength]);

  const loadData = () => {
    // bannerService.list().then((res) => setBanners(res.data));
    bannerService.getPaging(page, pageLength).then((res) => {
      setBanners(res.data);
      let items = [
        <Pagination.Item key="first" onClick={() => setPage(0)}>
          &laquo;
        </Pagination.Item>,
      ];
      for (let i = 0; i < res.pagingInfo.totalPages; i++) {
        items.push(
          <Pagination.Item
            key={i}
            active={i === page}
            onClick={() => setPage(i)}
          >
            {i + 1}
          </Pagination.Item>
        );
      }
      items.push(
        <Pagination.Item
          key="last"
          onClick={() => setPage(res.pagingInfo.totalPages - 1)}
        >
          &raquo;
        </Pagination.Item>
      );
      setPagingItems(items);
    });
  };
  const showEditModal = (e, id) => {
    if (e) e.preventDefault();
    if (id > 0) {
      // bannerService.getImageUrl(id).then((res) => {
      //   if (res.errorCode === 0) setAvatarPreview(res.data);
      //   else setAvatarPreview(defaultAvatar);
      // });
      bannerService.getImage(id).then((res) => {
        if (res.size > 0) setAvatarPreview(URL.createObjectURL(res));
        else setAvatarPreview(defaultAvatar);
      });
      bannerService.get(id).then((res) => {
        if (res.errorCode === 0) {
          formik.setValues(res.data);
          handleModalShow();
        }
      });
    } else {
      formik.resetForm();
      setAvatarPreview(defaultAvatar);
      handleModalShow();
    }
  };
  const handleDelete = (e, id) => {
    e.preventDefault();
    bannerService.remove(id).then((res) => {
      if (res.errorCode === 0) {
        toast.warn("Xa Thnh Cng");
        loadData();
      } else {
        toast.error(res.message + "Xa Tht Bi");
      }
    });
  };
  const handleSubmit = (data) => {
    if (data.Ban_ID === 0) {
      bannerService.add(data).then((res) => {
        if (res.errorCode === 0) {
          loadData();
          handleModalClose();
        }
      });
    } else {
      bannerService.update(data.Ban_ID, data).then((res) => {
        if (res.errorCode === 0) {
          loadData();
          handleModalClose();
          toast.success("Cp Nht Thnh Cng");
        } else {
          toast.error(res.message);
        }
      });
    }
  };
  const handleChangeAvatar = (e) => {
    if (e.target.files && e.target.files[0]) {
      setAvatarPreview(URL.createObjectURL(e.target.files[0]));
      formik.setFieldValue("banner", e.target.files[0]);
    }
  };
  return (
    <>
      <div className="container mt-4">
        <div className="card border-success bt-5">
          <div className="card-header">
            <div className="row">
              <div className="col">
                <h3 className="card-title text-success">Danh Sch nh Ba</h3>
              </div>
              <div className="col-auto">
                <button
                  type="button"
                  className="btn btn-success"
                  onClick={() => showEditModal(null, 0)}
                >
                  <i className="bi-plus-lg"></i> Add
                </button>
              </div>
            </div>
          </div>
          <div className="card-body">
            <div className="table-responsive">
              <table className="table table-bordered border-success table-hover table-striped">
                <thead>
                  <tr className="table-success border-success">
                    <th style={{ width: "50px" }}>STT</th>
                    <th>Tn nh Ba</th>
                    <th>Hnh nh </th>
                    <th>Thi Gian S Kin</th>
                    <th>a im T Chc</th>
                    <th>Ni Dung</th>
                    <th style={{ width: "100px" }}></th>
                  </tr>
                </thead>
                <tbody>
                  {banners.map((banner, idx) => (
                    <tr key={banner.Ban_ID}>
                      <td>{idx + 1}</td>
                      <td>{banner.banName}</td>
                      <td>
                        <img
                          className="img-fluid "
                          src={banner.banImg}
                          alt={banner.banName}
                        />
                      </td>
                      <td>{banner.banTime}</td>
                      <td>{banner.banPlace}</td>
                      <td>
                        <div
                          dangerouslySetInnerHTML={{ __html: banner.banDes }}
                        />
                      </td>

                      <td>
                        <a
                          href="/#"
                          onClick={(e) => showEditModal(e, banner.Ban_ID)}
                        >
                          <i className=" fa-2x  fa-solid fa-pen  text-success me-2"></i>
                        </a>
                        <a
                          href="/#"
                          onClick={(e) => handleDelete(e, banner.Ban_ID)}
                        >
                          <i className=" fa-2x fa-solid fa-trash text-success"></i>
                        </a>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
            <Pagination className="mt-3 mb-0 justify-content-end">
              {pagingItems}
            </Pagination>
          </div>
        </div>
      </div>
      <Modal
        show={modalShow}
        onHide={handleModalClose}
        backdrop="static"
        keyboard={false}
        size="xl"
      >
        <Modal.Header closeButton>
          <Modal.Title>
            {formik.values.Ban_ID > 0 ? "Chnh Sa nh Ba" : "Thm nh Ba"}
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <form>
            <Row>
              <Col sm="4" className="text-center">
                <img
                  src={avatarPreview}
                  alt=""
                  className="img-thumnail img-fluid"
                  style={{ height: "200px" }}
                />
                <input
                  type={"file"}
                  accept="image/*"
                  className="d-none form-control"
                  ref={inputFileRef}
                  onChange={handleChangeAvatar}
                ></input>
                <div className="mt-3 ms-0">
                  <Button
                    variant="success"
                    size="sm"
                    onClick={() => inputFileRef.current.click()}
                  >
                    Thm Hnh
                  </Button>
                </div>
              </Col>
              <Col sm="8">
                <Input
                  label="Tn nh Ba"
                  required
                  frmField={formik.getFieldProps("banName")}
                  errMessage={formik.touched.banName && formik.errors.banName}
                />
                <Input
                  label="Thi Gian S Kin"
                  required
                  frmField={formik.getFieldProps("banTime")}
                  errMessage={formik.touched.banTime && formik.errors.banTime}
                />
                <Input
                  label="a im T Chc"
                  required
                  frmField={formik.getFieldProps("banPlace")}
                  errMessage={formik.touched.banPlace && formik.errors.banPlace}
                />

                <label className="mb-3">Ni dung</label>
                <CKEditor
                  editor={FullEditor}
                  data={formik.values.banDes}
                  onBlur={(event, editor) => {
                    formik.setFieldValue("banDes", editor.getData());
                    console.log(editor.getData());
                  }}
                />
              </Col>
            </Row>
          </form>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleModalClose}>
            Close
          </Button>
          <Button
            variant="success"
            onClick={formik.handleSubmit}
            disabled={!formik.dirty || !formik.isValid}
          >
            Save
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
};

export default Banner;
