import React, { useState, useEffect } from "react";
import { useNavigate, useParams } from "react-router-dom";
import orderService from "./../services/orderService";
import TableRow from "./../components/TableRow";
const OrderDetail = () => {
  const navigate = useNavigate();
  const handleBack = () => {
    navigate("/ordermanagement");
  };
  const { id } = useParams();
  const [order, setOrder] = useState({
    Or_ID: 0,
    cusId: 0,
    date: "",
    totalCost: "",
    customer: {},
    details: [],
  });
  useEffect(() => {
    if (!/\d+/.test(id)) {
      navigate("/not-found");
    }
    orderService.get(id).then((res) => {
      if (res.errorCode === 0) {
        setOrder(res.data);
      } else {
        navigate("/not-found");
      }
    });
  }, [id, navigate]);
  var total = 0;
  console.log(order);
  return (
    <>
      {order.details.forEach((e) => {
        total += e.orDeQuantity * e.orDePrice;
        return total;
      })}

      <div className="container mt-3 pe-4 ps-4 ">
        <h1 className="text-center pb-3 text-success"> Thng tin n hng</h1>
        <div className="card border-primary bt-5 mb-5">
          <div className="card-header">
            <div className="row">
              <div className="col-5 mt-1 col-lg-2 fw-bold">
                <p>Khch hng:</p>
                <p>a ch: </p>
                <p>in thoi:</p>
                <p>Email:</p>
                <p>t ngy: </p>
              </div>
              <div className="col-7 mt-1 py-0 col-lg-7">
                <p>
                  {order.customer.lastName} {order.customer.firstName}
                </p>
                <p>{order.customer.address}</p>
                <p>{order.customer.phone}</p>
                <p>{order.customer.email}</p>
                <p>{order.date} </p>
              </div>
              <div className="col py-3 text-center col-lg-3">
                <div className="row">
                  <div className="col">
                    <p className=" ms-0 text-start fw-bold">M n hng:</p>
                  </div>
                  <div className="col text-start">
                    <p>{order.Or_ID}</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="row pt-2 ps-3">
            <p className="h4 text-success pt-1 pb-1 fw-bold">
              Danh sch t hng
            </p>
          </div>

          {/* table */}
          <div className="container">
            <table className="table table-striped table-bordered table-hover">
              <thead>
                <tr>
                  <th scope="col">STT</th>
                  <th scope="col">Tn Sn Phm</th>
                  <th scope="col">S Lng</th>
                  <th scope="col">Thnh Tin</th>
                </tr>
              </thead>
              <tbody>
                {order.details.map((e, idx) => (
                  <TableRow
                    key={idx}
                    stt={idx + 1}
                    name={e.product_detail.proName}
                    quantity={e.orDeQuantity}
                    price={e.orDePrice}
                  />
                ))}
                <tr>
                  <th scope="row" />
                  <td />
                  <td className="h5 text-danger">Tng tin: </td>
                  <td className="text-danger">{total}</td>
                </tr>
              </tbody>
            </table>
          </div>

          <div className="row mb-3 py-0">
            <div className="col text-center gy-1 ">
              <button
                type="submit"
                onClick={handleBack}
                className="btn btn-secondary btn-lg"
              >
                Tr V
              </button>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default OrderDetail;
