import api from "./api";
const list = () => api.get(api.url.banners);
const get = (id) => api.get(`${api.url.banners}/${id}`);
const add = (data) => {
  const formData = new FormData();
  for (const key in data) formData.append(key, data[key]);
  return api.post(api.url.banners, formData, {
    headers: {
      "Content-Type": "multipart/form-data",
    },
  });
};
const update = (id, data) => {
  const formData = new FormData();
  for (const key in data) formData.append(key, data[key]);
  return api.post(`${api.url.banners}/${id}`, formData, {
    headers: {
      "Content-Type": "multipart/form-data",
    },
  });
};
const remove = (id) => api.delete(`${api.url.banners}/${id}`);
const getImage = (id) => {
  return api.get(`${api.url.banners}/get-img/${id}`, {
    responseType: "blob",
  });
};
const getImageUrl = (id) => {
  return api.get(`${api.url.banners}/get-img-url/${id}`);
};
const getPaging = (pageNum, pageLength) => {
  const queryString = `p=${pageNum}&r=${pageLength}`;
  return api.get(`${api.url.banners}/get-paging?${queryString}`);
};
const bannerService = {
  list,
  get,
  add,
  update,
  remove,
  getImage,
  getImageUrl,
  getPaging,
};
export default bannerService;
